import {postWeb} from './http';

/**
 * 
 * @author liubaohuo
 * @param "name" 
 * @param func 
 * @param {this}
 */
Function.prototype.method = function(name, func) {
    if (!this.prototype[name]) {
        this.prototype[name] = func;
    }
    return this;
}

/**
 * Array
 * @author liubaohuo
 * @param filed <String> 
 * @param condition <Object> 
 * @return ""
 */
Array.method('findByCondition', function(field, condition) {
    let self = this;
    let item = self.find((element) => {
        let isSelected = true;
        for (let key in condition) {
            if (element[key] !== condition[key]) {
                isSelected = false;
                break;
            }
        }
        return isSelected;
    })
    return (item && item[field]);
})

/**
 * Array
 * @author liubaohuo
 * @param filed <String> 
 * @param condition <Object> 
 * @return [""]
 */
Array.method('filterByCondition', function(field, condition) {
    let self = this;
    let items = self.filter((element) => {
        let isSelected = true;
        for (let key in condition) {
            if (condition[key] instanceof Array) {
                let listTmp = condition[key].filter((subElement) => {
                    return element[key] === subElement;
                })
                if (!listTmp.length) {
                    isSelected = false;
                    break;
                }
            } else {
                if (element[key] !== condition[key]) {
                    isSelected = false;
                    break;
                }
            }
        }
        return isSelected;
    })
    let keyItems = [];
    items.forEach((item, index) => {
        keyItems.push(item[field]);
    })
    return keyItems;
})

/**
 * Array
 * @author liubaohuo
 * @param filed <String> 
 * @return [""]
 */
Array.method('concatAttr', function(field) {
    let self = this;
    let listTmp = [];
    self.forEach((item, index) => {
      if(typeof(item[field]) !== "undefined"){
        if (item[field] instanceof Array) {
          item[field].forEach((subItem, subIndex) => {
              listTmp.push(subItem);
          })
        }else{
          listTmp.push(item[field]);
        }
      }
    })
    return listTmp;
})

/**
 * Array
 * @author liubaohuo
 * @param item <Nubmer, Object> 
 * @return <Array> 
 */
Array.method('remove', function(item) {
    let self = this;
    let listTmp = [];
    listTmp = self.splice(item, 1);
    return listTmp;
})

/**
 * Date
 * @author liubaohuo
 * @param "fmt"
 * @return ""
 */
Date.method('format', function(fmt) {
    let o = {
        "M+": this.getMonth() + 1, //   
        "d+": this.getDate(), //   
        "h+": this.getHours(), //   
        "m+": this.getMinutes(), //   
        "s+": this.getSeconds(), //   
        "q+": Math.floor((this.getMonth() + 3) / 3), //   
        "S": this.getMilliseconds() //   
    };
    if (/(y+)/.test(fmt))
        fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
    for (var k in o)
        if (new RegExp("(" + k + ")").test(fmt))
            fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
    return fmt;
})

/**
 * String
 * @author liubaohuo
 * @return ""
 */
String.method('trim', function() {
    return this.replace(/(^\s*)|(\s*$)/g, "");
})