
let api = {
    getStudentInfo:'/student/getStudentInfo',//
    studentLogin:'/login/studentLoginWeb',//
    addStudent:'/student/addStudentWeb',//
    getGradeList:'/common/getGradeList',//
    getStudentList:'/login/getStudentListWeb',//
    getCurrentStudent:'/login/getCurrentStudent',//
    getTrainingTimeList:'/common/getTrainingTimeList',//
    //getStudentInfo:'/student/getStudentInfo',//
    updateStudentInfo:'/student/updateStudentInfoWeb',
    getStuCenterList:'login/getStudentExtendListWeb',//
    getLearningUserStudentSimpleList:'/learning/learningUserStudentSimpleList',//
    getLearningUserStudentSimpleHasComprehensiveStatisticsList:'/learning/learningUserStudentSimpleHasComprehensiveStatisticsList'//
}
import {postWeb} from "@/assets/js/http";

/**
 * user
 */
export default {
    namespaced: true,
    state: {
        //
        childInfo:{}
    },
    getters: {
        childInfo(state) {
            return state.childInfo;
        }
    },
    mutations: {
        updateChildInfo(state, payload){
            state.childInfo = payload
        },
        updateChildHeadImage(state,img){
            state.childInfo.head_image = img
        }
    },
    actions: {
        studentLoginActions({commit,dispatch},params){//
            return new Promise((resolve,reject)=>{
                postWeb(api.studentLogin, params).then((res)=>{
                    let {error_code,data} = res;
                    if(!error_code){
                        let info = data.current_student
    
                        commit('updateChildInfo',info)
                    }else{
                        dispatch('account/reset',{},{root:true})
                    }
                    resolve(res)
                },(err)=>{
                    dispatch('account/reset',{},{root:true})
                    reject(err)
                })
            })
        },
        addChildActions({commit,dispatch},params){
            return new Promise((resolve,reject)=>{
                postWeb(api.addStudent, params).then((res)=>{
                    let {error_code, data} = res;
                    if (!error_code){
                        commit('account/setStudentList', data,{root:true});
                    }
                    resolve(res)
                },(err)=>{
                    reject(err)
                })
            })
        },
        getGradeListActions({commit,dispatch},params){
            return new Promise((resolve,reject)=>{
                postWeb(api.getGradeList).then((res)=>{
                    resolve(res)
                },(err)=>{
                    reject(err)
                })
            })
        },
        getTrainingTimeListActions({commit,dispatch},params){
            return new Promise((resolve,reject)=>{
                postWeb(api.getTrainingTimeList).then((res)=>{
                    resolve(res)
                },(err)=>{
                    reject(err)
                })
            })
        },
        getStudentListActions({commit,dispatch},params){
            return new Promise((resolve,reject)=>{
                postWeb(api.getStudentList).then((res)=>{
                    let {error_code, data} = res;
                    if (!error_code){
                        commit('account/setStudentList', data.student_list,{root:true});
                    }
                    resolve(res)
                },(err)=>{
                    reject(err)
                })
            })
        },
        getStuCenterListActions({commit,dispatch},params){
            return new Promise((resolve,reject)=>{
                postWeb(api.getStuCenterList).then((res)=>{
                    resolve(res)
                },(err)=>{
                    reject(err)
                })
            })
        },
        getLearningUserStudentSimpleListActions({commit,dispatch},params){
            return new Promise((resolve,reject)=>{
                postWeb(api.getLearningUserStudentSimpleList).then((res)=>{
                    resolve(res)
                },(err)=>{
                    reject(err)
                })
            })
        },
        getLearningUserStudentSimpleHasComprehensiveStatisticsListActions({commit,dispatch},params){
            return new Promise((resolve,reject)=>{
                postWeb(api.getLearningUserStudentSimpleHasComprehensiveStatisticsList).then((res)=>{
                    resolve(res)
                },(err)=>{
                    reject(err)
                })
            })
        },
        getCurrentStudentActions({commit,dispatch},params){
            return new Promise((resolve,reject)=>{
                postWeb(api.getCurrentStudent).then((res)=>{
                    let {error_code,data} = res;
                    if(!error_code){
                        let info = data.current_student
                        commit('updateChildInfo',info)
                    }
                    resolve(res)
                },(err)=>{
                    reject(err)
                })
            })
        },
        getStudentInfoActions({commit,dispatch},params){
            return new Promise((resolve,reject)=>{
                postWeb(api.getStudentInfo,params).then((res)=>{
                    resolve(res)
                },(err)=>{
                    reject(err)
                })
            })
        },
        updateStudentInfoActions({commit,getters,dispatch},params){
            return new Promise((resolve,reject)=>{
                postWeb(api.updateStudentInfo,params).then((res)=>{
                    let {error_code,data} = res;
                    if(!error_code){
                        let info = JSON.parse(JSON.stringify(getters.childInfo))
                        info.name = params.name
                        commit('updateChildInfo',info)
                        dispatch('getStudentListActions')
                    }
                    resolve(res)
                },(err)=>{
                    reject(err)
                })
            })
        }
    }
}
  