import {postWeb} from "@/assets/js/http";
function createParams(params){
    let newParams = {
        origin_url:window.location.origin
    }
    newParams.origin_base_url = newParams.origin_url + params.to.fullPath
    for(let key in params){
        if(key!=='to'){
            newParams[key] = params[key]
        }
    }
    return newParams
}
